/*global PIXI*/
/*eslint no-undef: "error"*/
/*eslint no-undef: ["error", { "typeof": true }] */

export default class HexTileMap extends PIXI.Container {
    constructor(atlasData, tileSetTex, itemsData, itemsSetTex, mSignsData, mSignsSetTex) {
        super();

        this.clear = this.clear.bind(this);
        this.addTile = this.addTile.bind(this);
        this.addItem = this.addItem.bind(this);
        this.addMSign = this.addMSign.bind(this);

        this.tileSet = Object.assign({}, ...atlasData.SubTexture.map((texInfo) => {
            const rect = new PIXI.Rectangle(texInfo.x, texInfo.y, texInfo.width, texInfo.height);
            return {
                [texInfo.name] : 
                {
                    rect,
                    texture: new PIXI.Texture(tileSetTex, rect)    
                }
            };
        }));

        this.itemsSet = Object.assign({}, ...itemsData.SubTexture.map((texInfo) => {
            const rect = new PIXI.Rectangle(texInfo.x, texInfo.y, texInfo.width, texInfo.height);
            return {
                [texInfo.name] : 
                {
                    rect,
                    texture: new PIXI.Texture(itemsSetTex, rect)    
                }
            };
        }));

        this.mSignsSet = Object.assign({}, ...mSignsData.SubTexture.map((texInfo) => {
            const rect = new PIXI.Rectangle(texInfo.x, texInfo.y, texInfo.width, texInfo.height);
            return {
                [texInfo.name] : 
                {
                    rect,
                    texture: new PIXI.Texture(mSignsSetTex, rect)    
                }
            };
        }));
        console.log("--->", this.mSignsSet);
    }

    clear() {
        this.removeChildren();
    }

    addTile(name, x, y) {
        const tileTex = this.tileSet[name];
        if (tileTex) {
            const tileSpr = new PIXI.Sprite(tileTex.texture);
            tileSpr.x = x;
            tileSpr.y = y;
            this.addChild(tileSpr);
        }
    }

    addItem(name, x, y) {
        const itemTex = this.itemsSet[name];
        if (itemTex) {
            const itemSpr = new PIXI.Sprite(itemTex.texture);
            itemSpr.x = x;
            itemSpr.y = y;
            itemSpr.anchor.set(0.5, 1);
            this.addChild(itemSpr);
        }
    }

    addMSign(id, x, y) {
        let texArray = [
            this.mSignsSet[`/${id}1`].texture,
            this.mSignsSet[`/${id}2`].texture,
        ];
        if ( texArray[0] && texArray[1] ) {
            const mSpr = new PIXI.AnimatedSprite(texArray);
            mSpr.x = x;
            mSpr.y = y;
            mSpr.anchor.set(0.5, 1);
            mSpr.loop = true;
            mSpr.animationSpeed = 0.1;
            mSpr.gotoAndPlay((Math.random()<0.5)?0:1);
            this.addChild(mSpr);
        }
    }
}
