// CrystalMapData

// 1
const UnitBlockTexNum = 6;
// 1
const MapItemsNum = 19;
const MapItemsTreeNum = 4;

class CrystalMapData {
    constructor(line, row, homeRowNum) {
        // 
        this.line = line;
        this.row = row;
        this.blockMaxNum = line*row;
        // 
        this.mapDataBlocksData = null;
        // 
        this.homeRowNum = homeRowNum;
        this.homeX = 0;
        this.homeY = 0;
        // 
        this.monstersData = Array(this.blockMaxNum).fill(false);

        // bind
        this.createMapDataBlocks = this.createMapDataBlocks.bind(this);
        this.createRandMonsterData = this.createRandMonsterData.bind(this);

        // 
        this.createMapDataBlocks();
    }

    // 
    createRandMonsterData() {
        for (let i=0; i<this.blockMaxNum; i++) {
            if (this.mapDataBlocksData[i] && !this.mapDataBlocksData[i].hasCrystal) {
                this.monstersData[i] = (Math.random() < 0.2) ? true : false;
            }
        }
    }

    // 
    createMapDataBlocks() {
        this.mapDataBlocksData = Array(this.blockMaxNum).fill(null);

        let beginX = 0;
        let beginY = 0;
        let unitW = 100;
        let unitH = 50;
        let itemRate = 0.4;

        this.homeX = (unitW + unitW/4 + 1) + (unitW + unitW/2) * this.homeRowNum;
        this.homeY = unitW/4 + 1;

        let crystalsIdList = Array(this.blockMaxNum).fill(false);
        for (let i=0; i<100; i++) {
            crystalsIdList[(Math.round(Math.random()*(this.blockMaxNum-1)))] = true;
        }

        for (let l=0; l<this.line; l++) {
            let _maxRowNum = this.row;
            if ( (l%2) != 0 ) _maxRowNum -= 1;
            for (let r=0; r<_maxRowNum; r++) {
                let currid = l*this.row+r;
                let _texId = Math.round( Math.random()*UnitBlockTexNum ) + 1;
                if (_texId > UnitBlockTexNum) _texId = UnitBlockTexNum;
                let _itemNum = (Math.random() < 0.8) ? MapItemsTreeNum : MapItemsNum;
                let _data = {
                    line: l,
                    row: r,
                    currid: currid,
                    texid: _texId,
                    x: beginX + ( ((l%2) != 0)?(unitW-unitW/4):0 ) + r*(unitW + unitW/2),
                    y: beginY + l*(unitH/2),
                    hasCrystal: crystalsIdList[currid],
                    itemL: (Math.random() < itemRate) ? (Math.round( Math.random()*_itemNum ) + 1) : 0,
                    itemR: (Math.random() < itemRate) ? (Math.round( Math.random()*_itemNum ) + 1) : 0,
                }
                this.mapDataBlocksData[currid] = _data;
            }
        }
    }

    // 
    getBlocksTexName(id) {
        if (id === 0) return null;
        return `/${id}`;
    }
    // 
    getItemTexName(id) {
        if (id === 0) return null;
        return `/${id}`;
    }
    // 
    getHomeTexName() {
        return "/";
    }
    // 
    getCrystalTexName() {
        return "/";
    }
}

function CreateCrystalMap(homeRowNum=0, line=100, row=50) {
    return new CrystalMapData(line, row, homeRowNum);
}

export {
    CreateCrystalMap
};
