import React from 'react';
import { useState, useEffect, useRef } from 'react';

import { 
    Container,
    Row,
    Col,
    Card,
    CardTitle,
    CardBody,
    Button,
    InputGroup,
    InputGroupAddon,
    CustomInput,
    Form, 
    FormGroup, 
    Label, 
    Input, 
    FormText
} from '../../../components';

import { useScript, useSeqScripts } from '../../../hooks';
import { withModel } from "hox";

import useMonsterData from '../../../models/MonsterModel';
import { GenerateNFTs } from '../../../models/GenerateNFTs';
import { toRandChallengeForNum } from '../../../models/CombatModule';

function AniPanel(props) {
    const [inputNum, setInputNum] = useState("");
    const { level, levelUp, resetAvatarData, avatarData } = useMonsterData();

    return (
        <Card className="mb-3 ani-panel">
        <CardBody>
            <CardTitle tag="h6" className="mb-4">
                Forms Inline: Preview Example
                <span className="small ml-1 text-muted">
                    #2.01
                </span>
            </CardTitle>
            { /* START Form */}
            <Form inline>
                <FormGroup className="mb-2 mr-sm-2 mb-sm-0">
                    <Input type="text" name="text" id="enterName"
                            value={inputNum} placeholder="Enter Name..."
                            onChange = { (e) => {
                                const re = /^[0-9\b]+$/;
                                if (e.target.value === '' || re.test(e.target.value)) {
                                    setInputNum(e.target.value);
                                }
                            }}/>
                </FormGroup>
                <FormGroup className="mb-2 mr-sm-2 mb-sm-0">
                    <InputGroup>
                        <Input type="password" name="password" placeholder="Password..." />
                    </InputGroup>
                </FormGroup>
                <FormGroup className="mb-2 mr-sm-2 mb-sm-0">
                    <InputGroup>
                        <InputGroupAddon addonType="prepend">
                            Country
                        </InputGroupAddon>
                        <CustomInput type="select" id="country-selector-3" name="customSelect">
                            <option value="">Select...</option>
                            <option>United Kingdom</option>
                            <option>United States</option>
                            <option>Canada</option>
                            <option>Australia</option>
                            <option>New Zeland</option>
                        </CustomInput>
                    </InputGroup>
                </FormGroup>
                <FormGroup>
                    <CustomInput type="checkbox" id="rememberMe" label="Remember Me" inline />
                </FormGroup>
                <Button color="primary">
                    Submit
                </Button>
            </Form>
            { /* END Form */}

            { /* START Form */}
            <Form inline>
                level: {level}
                <Button color="primary" onClick={() => levelUp()}>
                    LevelUp
                </Button>
                <Button color="primary" onClick={() => resetAvatarData()}>
                    ResetAvatar
                </Button>
                <Button color="primary" onClick={() => console.log(avatarData)}>
                    Print
                </Button>
                <Button color="danger" onClick={() => GenerateNFTs()}>
                    Generate
                </Button>
                <Button color="primary" onClick={() => toRandChallengeForNum()}>
                    RandChallenge
                </Button>
            </Form>
            { /* END Form */}
        </CardBody>
    </Card>
    );
}

function getPixiType() {
    let type = "WebGL";
    if(!PIXI.utils.isWebGLSupported()){
        type = "canvas";
    }
    return type;
}

const LoadingView = ({ status }) => (
    <div>
        Script status: <b>{status}</b>
    </div>
);

const BACKGROUND_URL = "resource/background.png";

class _DragonbonesWidget extends React.Component {
    constructor(props) {
      super(props);

      this._loadResources.bind(this);

      this.viewRef = React.createRef();
      this.app = null;

      this._resources = props.resources || [];
      this._resources.push(BACKGROUND_URL);
      this._resources.push(
        "resource/avatar/MA_ske.dbbin"
        //"resource/avatar/MA_tex.json",
        //"resource/avatar/MA_tex.png",
        )

      for (let i=0; i<15; i++) {
        this._resources.push(
            `resource/avatar/MA_tex_${i}.json`,
            `resource/avatar/MA_tex_${i}.png`
        )
      }
      
      this._background = null;
      this._loader = null;
      this._scene = null;
    }

    componentDidMount() {
        this.app = new PIXI.Application({
            width: 500, height: 500, backgroundColor: 0xffffff, resolution: window.devicePixelRatio || 1,
        });

        this.viewRef.current.appendChild(this.app.view);
        
        this._loader = new PIXI.Loader();

        console.log("---LOADERS--->", this._loader, this._resources);
        this._loadResources();
    }

    componentWillUnmount() {
        this.viewRef.current.removeChild(this.app.view);
        this._background = null;
        this._loader = null;
    }

    _loadResources() {
        const binaryOptions = { loadType: PIXI.LoaderResource.LOAD_TYPE.XHR, xhrType: PIXI.LoaderResource.XHR_RESPONSE_TYPE.BUFFER };

        for (const resource of this._resources) {
            if (resource.indexOf("dbbin") > 0) {
                this._loader.add(resource, resource, binaryOptions);
            }
            else {
                this._loader.add(resource, resource);
            }
        }

        this._loader.load((loader, resources) => {
            this._pixiResources = resources;
            //
            //this._background = new PIXI.Sprite(this._pixiResources[BACKGROUND_URL].texture);
            //this._background.x = -this._background.texture.width * 0.5;
            //this._background.y = -this._background.texture.height * 0.5;
            //this.app.stage.addChild(this._background);

            var DragonBonesScene = require('./DragonbonesScene').default;

            // const factory = dragonBones.PixiFactory.factory;
            // factory.parseDragonBonesData(this._pixiResources["resource/mecha_1002_101d_show/mecha_1002_101d_show_ske.dbbin"].data);
            // const scene = new DragonBonesScene(
            //     this._pixiResources["resource/monster/TestAvatarBin_ske.dbbin"].data,
            //     this._pixiResources["resource/monster/TestAvatarBin_tex_0.json"].data,
            //     this._pixiResources["resource/monster/TestAvatarBin_tex_0.png"]
            // );

            let jsonDataList = []
            for (let i=0; i<15; i++) jsonDataList.push( this._pixiResources[`resource/avatar/MA_tex_${i}.json`].data );
            let texDataList = []
            for (let i=0; i<15; i++) texDataList.push( this._pixiResources[`resource/avatar/MA_tex_${i}.png`] );
            const scene = new DragonBonesScene(
                this._pixiResources["resource/avatar/MA_ske.dbbin"].data,
                jsonDataList, texDataList
                //this._pixiResources["resource/avatar/MA_tex.json"].data,
                //this._pixiResources["resource/avatar/MA_tex.png"]
            );

            this.app.stage.addChild(scene);
            this._scene = scene;

            //
            scene._onStart();
            // this._startRenderTick(); // Make sure render after dragonBones update.
        });
    }

    componentDidUpdate(prevProps) {
        const { monsterData } = this.props;
        if(this._scene) this._scene.setAvatarData(monsterData.avatarData);
    }

    render() {
        return <div ref={this.viewRef} />;
    }
}

const DragonbonesWidget = withModel(useMonsterData, monsterData => ({
    monsterData
}))(_DragonbonesWidget);

const _pixi_scripts = [
    "https://cdnjs.cloudflare.com/ajax/libs/pixi.js/6.1.3/browser/pixi.min.js",
    "/js/dragonbones.min.js"
];

function AniViewPixi() {
    /*global PIXI, dragonBones*/
    /*eslint no-undef: "error"*/
    /*eslint no-undef: ["error", { "typeof": true }] */

    const scripts_status = useSeqScripts(_pixi_scripts);

    const ready = scripts_status === "ready";

    return (
        <Card className="mb-3 ani-panel">
        <CardBody>
            {
                ready ? <DragonbonesWidget /> : <LoadingView status={scripts_status}/>
            }
        </CardBody>
        </Card>
    );
}

function AniViewer(props) { 
    
    return (
    <React.Fragment>
        <Container className="ani-panel">
            { /* START Header 1 */}
            <div>"Forms 1"</div>
            { /* END Header 1 */}
            { /* START Section 1 */}
            <Row>
                <Col lg={ 12 }>
                    <AniPanel/>
                </Col>
            </Row>
            { /* END Section 1 */}

            { /* START Section 2 */}
            <Row>
                <Col lg={ 12 }>
                    <AniViewPixi/>
                </Col>
            </Row>
            { /* END Section 2 */}
        </Container>
    </React.Fragment>
    );
}

export default AniViewer;