import React from 'react';
import { useState, useEffect, useRef } from 'react';

import { 
    Container,
    Row,
    Col,
    Card,
    CardTitle,
    CardBody,
    Button,
    InputGroup,
    InputGroupAddon,
    CustomInput,
    Form, 
    FormGroup, 
    Label, 
    Input, 
    FormText
} from '../../../components';

import { useScript, useSeqScripts } from '../../../hooks';
import { withModel } from "hox";

import { CreateCrystalMap } from '../../../models/CrystalMapData';
import useCrystalMapData from '../../../models/CrystalMapModel';

class _CrystalMapWidget extends React.Component {
    constructor(props) {
      super(props);

      this._loadResources.bind(this);

      this.viewRef = React.createRef();

      this.app = null;
      this._loader = null;

      this.windowWidth = this.props.width || 1000;
      this.windowHeight = this.props.height || 600;

      this._resources = props.resources || [];
      this._resources.push("resource/map1/MapBlocks_tex.json", "resource/map1/MapBlocks_tex.png");
      this._resources.push("resource/map1/MapItems_tex.json", "resource/map1/MapItems_tex.png");
      this._resources.push("resource/map1/MSign_tex.json", "resource/map1/MSign_tex.png");
    }

    componentDidMount() {
        this.app =  PIXI.autoDetectRenderer({ 
            width: this.windowWidth, height: this.windowHeight, backgroundColor: 0x01356a });

        this.viewRef.current.appendChild(this.app.view);
        
        this._loader = new PIXI.Loader();

        this._loadResources();
    }

    componentWillUnmount() {
        this.viewRef.current.removeChild(this.app.view);
        this._loader = null;
    }

    _loadResources() {
        for (const resource of this._resources) {
            this._loader.add(resource, resource);
        }

        this._loader.load((loader, resources) => {
            this._pixiResources = resources;

            const atlasData = resources["resource/map1/MapBlocks_tex.json"].data;
            const tileSetTex = resources["resource/map1/MapBlocks_tex.png"].texture;
            const itemsData = resources["resource/map1/MapItems_tex.json"].data;
            const itemsSetTex = resources["resource/map1/MapItems_tex.png"].texture;
            const mSignsData = resources["resource/map1/MSign_tex.json"].data;
            const mSignsSetTex = resources["resource/map1/MSign_tex.png"].texture;

            var HexTileMap = require('./HexTileMap').default;

            this.tileMap = new HexTileMap(
                atlasData, tileSetTex, itemsData, itemsSetTex, mSignsData, mSignsSetTex
                );

            // 
            const mapLine = 100;
            const mapRow = 50;
            let cmap = CreateCrystalMap(Math.round((mapRow-2)/2), mapLine, mapRow);
            // 
            cmap.createRandMonsterData();
            // 
            this.tileMap.addItem(cmap.getHomeTexName(), cmap.homeX, cmap.homeY);
            // 
            cmap.mapDataBlocksData.forEach( (_data) => {
                if (_data) {
                    this.tileMap.addTile(cmap.getBlocksTexName(_data.texid), _data.x, _data.y);
                }
            } );
            // 
            cmap.mapDataBlocksData.forEach( (_data) => {
                if (_data) {
                    if (_data.itemL > 0) this.tileMap.addItem(cmap.getItemTexName(_data.itemL), _data.x+(Math.random()*15), _data.y+20+(Math.random()*10));
                    if (_data.itemR > 0) this.tileMap.addItem(cmap.getItemTexName(_data.itemR), _data.x+70+(Math.random()*15), _data.y+20+(Math.random()*10));
                    // 
                    if (_data.hasCrystal) {
                        this.tileMap.addItem(cmap.getCrystalTexName(), _data.x+50, _data.y+40);
                    }
                    else {
                        // test
                        if (cmap.monstersData[_data.currid]) {
                            this.tileMap.addMSign(Math.round(Math.random()*6)+1, _data.x+50, _data.y+45);
                        }
                    }
                }
            } );
            
            this.tileMap.x = -(cmap.homeX - this.windowWidth/2);
            this.tileMap.y = -(cmap.homeY - this.windowHeight/2);

            const { mapData } = this.props;
            mapData.setMapXY(this.tileMap.x, this.tileMap.y);

            let stage = new PIXI.Container();
            stage.addChild(this.tileMap);
        
            PIXI.Ticker.shared.add(() => this.app.render(stage));
        });
    }

    componentDidUpdate(prevProps) {
        const { mapData } = this.props;

        this.tileMap.x = mapData.mapX;
        this.tileMap.y = mapData.mapY;
    }

    render() {
        return <div ref={this.viewRef} />;
    }
}
const CrystalMapWidget = withModel(useCrystalMapData, (mapData) => ({
    mapData
}))(_CrystalMapWidget);

function MapViewPixi() {
    /*global PIXI*/
    /*eslint no-undef: "error"*/
    /*eslint no-undef: ["error", { "typeof": true }] */

    const scripts_status = useSeqScripts([
        "https://cdnjs.cloudflare.com/ajax/libs/pixi.js/6.1.3/browser/pixi.min.js"
    ]);

    const ready = scripts_status === "ready";

    return (
        <Card className="mb-3 ani-panel">
        <CardBody>
            {
                ready ? <CrystalMapWidget width={1000} height={600}/> : <div>Loading...</div>
            }
        </CardBody>
        </Card>
    );
}

function MapViewer(props) { 
    const { mapX, mapY, setMapX, setMapY, setMapXY } = useCrystalMapData();
    
    return (
    <React.Fragment>
        <Container className="ani-panel">
            { /* START Header 1 */}
            <div>"Forms 1"</div>
            { /* END Header 1 */}
            { /* START Section 1 */}
            <Row>
                <Col>
                <img src="img/avatar.png"/>
                <img src="img/egg1.png"/>
                <img src="img/egg2.png"/>
                <img src="img/egg3.png"/>
                <img src="img/island1.png"/>
                <img src="img/lock.png"/>
                <img src="img/island2.png"/>
                <img src="img/logo.png"/>
                </Col>

                <Col lg={ 12 }>
                <Form inline>
                <Button color="primary" onClick={() => setMapX(mapX+100)}>
                    
                </Button>
                <Button color="primary" onClick={() => setMapX(mapX-100)}>
                    
                </Button>
                <Button color="primary" onClick={() => setMapY(mapY+100)}>
                    
                </Button>
                <Button color="primary" onClick={() => setMapY(mapY-100)}>
                    
                </Button>
                <Button color="danger" onClick={() => setMapXY(-350, 150)}>
                    Reset
                </Button>
                </Form>
                </Col>
            </Row>
            { /* END Section 1 */}

            { /* START Section 2 */}
            <Row>
                <Col lg={ 12 }>
                    <MapViewPixi/>
                </Col>
            </Row>
            { /* END Section 2 */}
        </Container>
    </React.Fragment>
    );
}

export default MapViewer;