import { useState } from "react";
import { createModel } from "hox";

import { CreateRandomMonsterEgg } from "./MonsterData";

const _MonsterData = CreateRandomMonsterEgg(2);

function useMonsterData() {
    const [grade, setGrade] = useState(_MonsterData.grade);
    const [level, setLevel] = useState(0);
    const [avatarData, setAvatarData] = useState(_MonsterData.avatarData);
  
    const levelUp = () => {
        _MonsterData.levelUp();
        setLevel(_MonsterData.level);
        setAvatarData(_MonsterData.avatarData);
    };

    const resetAvatarData = () => {
        _MonsterData.resetAvatarData();
        setLevel(_MonsterData.level);
        setAvatarData(_MonsterData.avatarData);
    };
  
    return {
        level,
        levelUp,
        avatarData,
        resetAvatarData,
        grade,
    };
}

export default createModel(useMonsterData);