
import { CreateRandomMonsterEgg } from "./MonsterData";

const _GenerateMonsterData = CreateRandomMonsterEgg(2);

function GenerateNFTs(num=10000) {
    // 
    let totalScore = 0;
    let minScore = 99999;
    let maxScore = 0;

    // 
    let _nfts = [];
    // 
    for (let i=0; i<num; i++) {
        _GenerateMonsterData.resetAvatarData();
        for (let lvl=0; lvl<10; lvl++) {
            _GenerateMonsterData.levelUp();
        }
        let newData = {
            score: _GenerateMonsterData.score,
            level: _GenerateMonsterData.level,
            skillType: _GenerateMonsterData.skillType,
            ..._GenerateMonsterData.avatarData
        };
        _nfts.push(newData);
        // 
        if (newData.score < minScore) minScore = newData.score;
        if (newData.score > maxScore) maxScore = newData.score;
        totalScore += newData.score;
    }

    console.log(_nfts);
    console.log("MinScore:", minScore);
    console.log("MaxScore:", maxScore);
    console.log("Average:", totalScore/_nfts.length);
}

export {
    GenerateNFTs
};
