import React from 'react';
import {
    Route,
    Switch,
    Redirect
} from 'react-router';

// ----------- Pages Imports ---------------
import Error404 from './../../routes/Pages/Error404';

import DefaultDashboard from './../../routes/Dashboards/Home';
import AniViewer from '../../routes/Dashboards/AniViewer';
import MapViewer from '../../routes/Dashboards/MapViewer';

// import ComingSoon from './Pages/ComingSoon';
// import Confirmation from './Pages/Confirmation';
// import Danger from './Pages/Danger';
// import ForgotPassword from './Pages/ForgotPassword';
// import LockScreen from './Pages/LockScreen';
// import Login from './Pages/Login';
// import Register from './Pages/Register';
// import Success from './Pages/Success';
// import Timeline from './Pages/Timeline';

//------ Route Definitions --------
// eslint-disable-next-line no-unused-vars
export const RoutedContent = () => {
    return (
        <Switch>
            { <Redirect from="/dashboard.html" to="/dashboard" exact /> }

            { <Route path="/dashboard" exact component={DefaultDashboard} /> }

            { <Route path="/aniviewer" exact component={AniViewer} /> }
            { <Route path="/mapviewer" exact component={MapViewer} /> }

            { /*    Pages Routes    */ }
            { <Route component={ Error404 } path="/dashboard/error-404" /> }
            {/* <Route component={ ComingSoon } path="/pages/coming-soon" />
            <Route component={ Confirmation } path="/pages/confirmation" />
            <Route component={ Danger } path="/pages/danger" />
            <Route component={ Error404 } path="/pages/error-404" />
            <Route component={ ForgotPassword } path="/pages/forgot-password" />
            <Route component={ LockScreen } path="/pages/lock-screen" />
            <Route component={ Login } path="/pages/login" />
            <Route component={ Register } path="/pages/register" />
            <Route component={ Success } path="/pages/success" />
            <Route component={ Timeline } path="/pages/timeline" />

            { /*    404    */ }
            <Redirect to="/dashboard/error-404" />
        </Switch>
    );
};

//------ Custom Layout Parts --------
export const RoutedNavbars  = () => (
    <Switch>
        { /* Other Navbars: */}
        { /* Default Navbar: */}
        <Route
            component={ DefaultDashboard.Navbar }
        />
    </Switch>  
);

export const RoutedSidebars = () => (
    <Switch>
        { /* Other Sidebars: */}
        {/* <Route
            component={ SidebarASidebar }
            path="/layouts/sidebar-a"
        /> */}
        { /* Default Sidebar: */}
        <Route
            component={ DefaultDashboard.Sidebar }
        />
    </Switch>
);
