// CrystalMapModel

import { useState } from "react";
import { createModel } from "hox";

function useCrystalMapData() {
    const [mapX, setMapX] = useState(-350);
    const [mapY, setMapY] = useState(150);
  
    return {
        mapX,
        mapY,
        setMapX,
        setMapY,
        setMapXY: (x, y) => {setMapX(x); setMapY(y);},
    };
}

export default createModel(useCrystalMapData);